.TodoItem {
  background-color: #FAFAFA;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
  border-radius: 10px;
}

.TodoItem-p {
  color: #1E1E1F;
  margin: 24px 0 24px 80px;
  width: 400px;
  /* width: calc(100% - 120px); */
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
  text-align: left;
}
.TodoItem-p--complete {
  text-decoration: line-through;
}